            <th>装柜日</th>
            <th>预计到港日</th>
            <!-- <th>预计入仓日期</th> -->
            <th>实际入仓日期</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in tableData" :key="index">
            <td>{{ index + 1 + (page - 1) * 50 }}</td>
            <td>{{ item.sku }}</td>
            <td>{{ item.store }}</td>
            <td>{{ item.product_type }}</td>
            <td>{{ item.container_code }}</td>
            <td>{{ item.plan_order }}</td>
            <td>{{ item.warehouse_type }}</td>
            <td>{{ item.warehouse_name }}</td>
            <td>{{ item.delivery_date }}</td>
            <td>{{ item.plan_arrival_date }}</td>
            <!-- <td>{{ item.delivery_date }}</td> -->
            <td>{{ item.storage_date }}</td>
          </tr>
        </tbody>
      </table>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="page"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pagetotle"
        :page-size="50"
        style="margin:1vh 0 0 27vw;"
      ></el-pagination>
    </div>
